
table{
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
}

td{
    width: 50%;
    height: 300px;
    text-align: center;
}

.row img{
    max-width: 500px;
    max-height: 300px;
}

.row p{
    font-size: 1.2em;
    margin: 0 50px;
    text-align: justify;
    line-height: 1.5em;
}

.download-rapport{
    border: 3px solid #111;
    padding: 10px 30px;
    font-size: 1.3em;
    color: #000;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-weight: 500;
    transition: border 0.4s, background 0.4ms;
}

.download-rapport:hover{
    color: #111;
    background: #bdfbff;
    border: 3px solid #bdfbff;
    transition: 0.4s all ease;
}